



















/* eslint-disable no-unused-expressions */
import Vue from 'vue';
import { mapActions } from 'vuex';

export default Vue.extend({
  name: 'Cell',
  props: {
    cell: Object,
  },
  methods: {
    ...mapActions({
      openCell: 'mines/openCell',
      openEmptyCell: 'mines/openEmptyCell',
      toggleFlagCell: 'mines/toggleFlagCell',
    }),

    handleOpenClick() {
      const {
        is_open,
        col, row,
        is_flagged,
        bombs_around,
        has_bomb,
      } = this.cell;

      if (is_open || is_flagged) {
        return null;
      }

      const cell_position = {
        col,
        row,
      };

      if (bombs_around === 0 && !has_bomb) {
        this.openEmptyCell(cell_position);
        return null;
      }

      this.openCell(cell_position);

      return null;
    },

    handleToggleFlagClick() {
      const { is_open, col, row } = this.cell;
      if (is_open) {
        return null;
      }

      const cell_position = {
        col,
        row,
      };

      this.toggleFlagCell(cell_position);

      return null;
    },
  },
});
